import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom'; 
import '../css/logoutDevices.css'
import PasswordView from '../../PasswordView'
import PasswordModal from './PasswordModal';

export default function LogoutDevices(){
    const navigate = useNavigate()
    const userId = 'userId';

    const [inputView, setInputView] = useState(false); //  view on/off
    const [inputValue, setInputValue] = useState(''); //input 
    const [modal, setModal] = useState(false);

    const { inputIcon, inputType } = PasswordView(inputView, inputValue);

    const toggleinputView = () => {
        if(inputView){
            setInputView(false)
        }else{
            setInputView(true)
        }
    }

    const inputValueHandler = (e)=>{
        setInputValue(e.target.value)
    }

    return(
        <div className="change-detail">
            {modal ? <PasswordModal setModal={setModal}/> : null}
            <div className="change-title">
                <h2>  </h2>
            </div>
            <span><p>   .    .     4   .</p></span>
            <form action="">
                <fieldset>
                    <legend></legend>
                    <label></label>
                    <span>
                        <input type={inputType} placeholder=' ' onChange={inputValueHandler}/>
                        <button type="button" className='view-control' onClick={()=>{toggleinputView()}}>
                            <span className="material-symbols-outlined icon">{inputIcon}</span>
                        </button>
                        <div className='input-bottom'>
                            <div className="text">( )</div>
                        </div>
                    </span>
                </fieldset>
                <div className="button-area">
                    <button className='save' type="button" onClick={()=>{setModal(true)}}></button>
                    <button className='cancel' type="button" onClick={()=>{navigate(-1)}}></button>
                </div>
                <button className='find-password'> </button>
            </form>
        </div>
    )
}