import React from 'react';
import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';

export default function ContentTabText(props) {
    // console.log(props.data)
    const contentDetail = useSelector((state)=>state.contentDetail)
    const {contentType} = useParams()
    const RunTime = contentDetail.runtime < 60 ? `${contentDetail.runtime}` 
                    : `${Math.floor(contentDetail.runtime/60)} ${contentDetail.runtime%60}`
    const ProductionCompanies = contentDetail.production_companies.length > 0 ? contentDetail.production_companies: null;
    
    return (
        <div className='tab-info'>
            <div className='title'>{contentType == 'movie' ? contentDetail.title : contentDetail.name}</div>
            <div className="detail">
                <div className="box1"><p>{props.data.overview}</p></div>
                <div className="box2">
                    <div className="etc-info">
                        <h6> :</h6>
                        <p>{contentType == 'movie' ? RunTime : contentDetail.episode_run_time+''}</p>
                    </div>
                    <div className="etc-info">
                        <h6>:</h6>
                        <p>{contentType == 'movie' ? contentDetail.release_date : contentDetail.first_air_date}</p>
                    </div>
                    <div className="etc-info">
                        <h6>:</h6>
                        <p>{props.genre.join(', ')}</p>
                    </div>
                    <div className="etc-info">
                        <h6>:</h6>
                        <img className='img1' src='https://disney.images.edge.bamgrid.com/ripcut-delivery/v1/variant/disney/8B03E101F8D8DA2184622BB0513EA1F2C5BD0B545E1F363A7288B204D8F32EAE/scale?width=240'/>
                    </div>
                </div>
                <div className="box2">
                    <div className="etc-info">
                        <h6>:</h6>
                        {
                            ProductionCompanies != null ?
                            <img className='img1' src={`https://image.tmdb.org/t/p/original${ProductionCompanies[0].logo_path}`}/>:<p> </p>
                        }
                        {
                            ProductionCompanies != null && ProductionCompanies[1] 
                            ? <img className='img1' src={`https://image.tmdb.org/t/p/original${ProductionCompanies[1].logo_path}`}/> :null
                        }
                    </div>
                    <div className="etc-info">
                        <h6>:</h6>
                        <p> </p>
                    </div>
                </div>
            </div>
        </div>
    );
}

