import PasswordView from '../../PasswordView'
import ModifyLogic from '../../BackEndData/ModifyLogic';
import '../css/passwordModal.css'

import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom'; 
import { setMovie } from '../../store/store';


export default function PasswordModal(props) {
    const navigate = useNavigate()
    const { userId } = useParams() 
    const [inputView, setInputView] = useState(false); //  view on/off
    const [inputValue, setInputValue] = useState(''); //input 

    const { inputIcon, inputType } = PasswordView(inputView, inputValue);
    const {userInput,  handleInput, ModifyData} = ModifyLogic(userId, props.changeData)
    
    const toggleinputView = () => {
        if(inputView){
            setInputView(false)
        }else{
            setInputView(true)
        }
    }

    const inputValueHandler = (e)=>{
        setInputValue(e.target.value)
    }

    return (
        <div className="password-modal-container">
            <div className='password-modal'>
                <h2> </h2>
                <form action="">
                <fieldset>
                    <legend></legend>
                    <span>
                        <input type={inputType} placeholder='' onChange={(e)=>{inputValueHandler(e); handleInput(e)}} value={userInput} required/>
                        <button type="button" className='view-control' onClick={()=>{toggleinputView()}}>
                            <span className="material-symbols-outlined icon">{inputIcon}</span>
                        </button>
                        <div className='input-bottom'>
                            <div className="text">( )</div>
                        </div>
                    </span>
                </fieldset>
                <div className="button-area">
                    <button className='save' type="button" onClick={()=>{ModifyData(); navigate('/profile/user1234/account')}}></button>
                    <button className='cancel' type="button" onClick={()=>{props.setModal(false)}}></button>
                </div>
                <button className='find-password'> </button>
            </form>
            </div>
        </div>
        
    );
}

