import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom'; 
import '../css/changeEmail.css'
import PasswordModal from './PasswordModal';

export default function ChangeEmail(){
    const [modal, setModal] = useState(false);
    const navigate = useNavigate()
    const userId = 'userId';
    const currentEmail = 'bombom@younsung.ac.kr';

    const [checkOn, setCheckOn] = useState(false);
    const [hideText, setHideText] = useState('');

    const toggleCheckOn = () => {
        if(checkOn){
            setCheckOn(false)
        }else{
            setCheckOn(true)
        }
    }

    useEffect(()=>{
        if(checkOn){
            setHideText('     .      4   .')
        }else{
            setHideText()
        }
    },[checkOn])

    return(
        <div className="change-detail">
            {modal ? <PasswordModal setModal={setModal} changeData={'email'}/> : null}
            <div className="change-title">
                <h2> </h2>
            </div>
            <span><p>+        .</p></span>
            <p className='currunt'> : <span>{currentEmail}</span></p>
            <form action="">
                <fieldset>
                    <legend>currentEmail</legend>
                    <label> </label>
                    <span>
                        <input type="text" placeholder={currentEmail}/>
                        <div className='input-bottom'></div>
                    </span>
                </fieldset>
                <div className='check-box' onClick={()=>{toggleCheckOn()}}>
                    <input/>
                    <div className='check-icon'>{checkOn?<span class="material-symbols-outlined icon">done</span>:null}</div>
                    <div><div className='check-text'>  
                        <div className="hide-text">{hideText}</div>
                    </div></div>
                </div>
                <div className="button-area">
                    <button className='save' type="button" onClick={()=>{setModal(true)}}></button>
                    <button className='cancel' type="button" onClick={()=>{navigate(-1)}}></button>
                </div>
            </form>
        </div>
    )
}