import React, { useState, useEffect } from 'react';
import useScroll from './useScroll.js';
import { useSelector, useDispatch } from 'react-redux';

//       
export default function ScrollLoading(DataList, data1, data2, scroll) {
    const [data, setData] = useState([]); //      
    const [count, setCount] = useState(0);//   

    useEffect(()=>{
        // data state  trendMovies      trendMovies    
        // state,    trendMovies  ?

        if (data1 && data2) {
            //    
            setData(DataList.slice(0, 4));  
            setCount(data.length);
        }
    },[data1,data2])

    useEffect(()=>{
        if(scroll+window.innerHeight +5 >= document.documentElement.scrollHeight
            && count < DataList.length){//  
            setCount(count+2)    
        }
    },[count, scroll])

    useEffect(()=>{//count   data  
        if(count < 4){
            setData(DataList.slice(0,4))//  4  
        }else{
            setData(DataList.slice(0,count))
        }

    },[count])

    return {data}
}